import React from 'react';
import './App.css';
import {Route, Router} from 'react-router-dom'
import history from './util/History'
import { mageSpecs, graphs, VODS } from './config/Config';
import RawData from './components/sims/RawData';
import SimsComponent from './components/sims/SimsComponent';
import GuidesComponent from './components/guides/GuidesComponent';
import Header from './components/header/Header';
import VODsComponent from './components/vods/VODsComponent';
import FAQComponent from './components/faq/FAQComponent';


export const APP_PATHS = {
 sims: '/sims',
 html: '/html',
 raid: '/raid',
 dungeon: '/dungeon'
}

function App() {
  return (
    <div className="App">
      <Header/>
      <div className="app-page">
        <Router history={history}>
          <Route 
            exact path={APP_PATHS.sims}
            render={() => (<SimsComponent specs={mageSpecs} graphs={graphs}/>)}
          />
          <Route 
            exact path="/sims/html" 
            render={() => (<RawData/>)}
          />
          <Route 
            exact path={APP_PATHS.html}
            render={() => (<RawData/>)}
          />
          <Route 
            exact path="/guides" 
            render={() => (<GuidesComponent/>)}
          />
          <Route 
            exact path="/vods" 
            render={() => (<VODsComponent vods={VODS.bosses}/>)}
          />
           <Route 
            exact path={APP_PATHS.raid}
            render={() => (<VODsComponent vods={VODS.bosses}/>)}
          />
           <Route 
            exact path={APP_PATHS.dungeon}
            render={() => (<VODsComponent vods={VODS.dungeons}/>)}
          />
          <Route 
            exact path="/faq" 
            render={() => (<FAQComponent/>)}
          />
        </Router>
      </div>
    </div>
  );
}

export interface Player {
  name: string,
  spec: string,
  dps: DPS
}

export interface DPS {
  mean: number,
  min: number,
  max: number
}


export default App;
