import React from "react";
import { Player } from "../../App";
import { targets } from "../../config/Config";
import { formatText } from "../../util/Util";
import { RawLinkStyle, Spec } from "./SimsComponent";
import styled from "styled-components";

type Props = {
    changeSpecSelection: (selection: string) => void,
    changeGraphSelection: (selection: string) => void,
    changeTargetSelection: (selection: string) => void,
    selectedSpec: string,
    selectedGraph: string,
    selectedTarget: string,
    specs: Array<Spec>,
    graphs: Array<Spec>,
}

interface State {
}


export interface Graph {
    players: Array<Player>
}

export default class ButtonBar extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            val: "hello"
        }
    }


    render() {

        var specButtons = this.props.specs.map((it) => {
            let classColor: string = "mage"
            let selectedClass : string = this.props.selectedSpec === it.ref ? `${classColor}-selected` : "nav-button spec"
           return <button key={it.ref} className={`nav-button ${classColor} ${selectedClass}`} onClick={() => this.props.changeSpecSelection(it.ref)}>{ it.displayName }</button>
        })

        var targetButtons = [];
        for (const [key, value] of Object.entries(targets)) {
            let selectedClass : string = this.props.selectedTarget === value ? "nav-button target-selected" : "nav-button target"
            targetButtons.push( <button key={key} className={selectedClass} onClick={() => this.props.changeTargetSelection(value)}>{ formatText(key) }</button> )
        }

        var graphButtons = this.props.graphs.map((it) => {
            let selectedClass : string = this.props.selectedGraph === it.ref ? "nav-button graph-selected" : "nav-button graph"
            return <button key={it.ref} className={selectedClass} onClick={() => this.props.changeGraphSelection(it.ref)}>{ it.displayName }</button>
        })

               return ( 
                <div className={'header-div'}>
                <RawLinkStyle to={`/html`}>HTML Reports</RawLinkStyle>
            <div className="navbar">
                    <StyledButtonRow wrap={false} >
                        { specButtons }
                    </StyledButtonRow>

                    <StyledButtonRow wrap={false} >
                        { targetButtons }
                    </StyledButtonRow>

                    <StyledButtonRow wrap={true} >
                        { graphButtons }
                    </StyledButtonRow>
                </div>
                </div>
             )
    }
}

interface StyledButtonRowProps {
    wrap: boolean
}

const StyledButtonRow = styled.div<StyledButtonRowProps>`
display: flex;
justify-content: center;
align-content: space-between;
flex-wrap: ${props => props.wrap ? 'wrap' : 'nowrap' }
`