import { VODS } from '../../config/Config';

interface VodRow {
    name: string,
    image: string,
    fire: Spec[],
    frost: Spec[],
    arcane: Spec[],
}

interface Spec {
    displayName: string,
    link: string
}

interface Props {
    vods: VodRow[]
}
export default function VODsComponent(props: Props) {
    const vods = props.vods
    return (
        <div className={'vods-div'}>
            <table>
                <tr>
                    <th></th>
                    <th></th>
                    <th>Arcane</th>
                    <th>Frost</th>
                    <th>Fire</th>
                </tr>
                {
                    vods.map(vod => {
                        return <tr>
                            <td className={"boss-icon"}>
                                <img src={`bosses/${vod.image}`} className={"boss-image"} alt={vod.name} />
                            </td>
                            <td className={"boss-square"}>
                                <div className={"boss-name"}>{vod.name}</div>
                            </td>
                            <td className={"spec-square arcane"}>
                                {vod.arcane.map(it => { return link(it.link, it.displayName) })}
                            </td>
                            <td className={"spec-square frost"}>
                                {vod.frost.map(it => { return link(it.link, it.displayName) })}
                            </td>
                            <td className={"spec-square fire"}>
                                {vod.fire.map(it => { return link(it.link, it.displayName) })}
                            </td>
                        </tr>
                    })
                }
            </table>
        </div>


    )
}


function link(url: string, player: string) {
    return (<div className="rowContent"><a href={url}>{player}</a></div>)
}